<template>
    <div class="overflow-x-auto">
        <div class="inline-block min-w-full overflow-hidden">
            <div class="table-container">
                <table class="min-w-full">
                    <thead>
                        <tr>
                            <th class="border px-4 py-2 text-xs" v-for="header in headerList" :key="header">{{
                                header }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="paginatedItems.length === 0">
                            <td class="border px-4 py-4 text-xs text-center" :colspan="headerList.length">No se encontr
                                ningn dato
                                para esta tabla</td>
                        </tr>
                        <!-- Iterar sobre los elementos de paginatedItems -->
                        <tr v-else v-for="(item, index) in paginatedItems" :key="index">
                            <td class="border px-4 py-2 text-xs">{{ item.coderecarga }}</td>
                            <td class="border px-4 py-2 text-xs">{{ item.proveedor }}</td>
                            <td class="border px-4 py-2 text-xs">{{ item.codcisterna }}</td>
                            <td class="border px-4 py-2 text-xs">{{ item.galones }}</td>
                            <td class="border px-4 py-2 text-xs">{{ item.fecha.split(' ')[0] }}</td>
                            <td class="border px-4 py-2 text-xs">{{ item.fecha.split(' ')[1] }}</td>
                            <td class="border px-4 py-2 text-xs">{{ item.codempresa }}</td>
                            <td class="border px-4 py-2 text-xs flex items-center">
                                <button @click="viewImage(item.pathImage)"
                                    class="p-2 rounded-lg bg-orange-400 text-white h-[30%]">
                                    <i class="fa-solid fa-image"></i>
                                </button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
    <div class="container-nav-pages mt-4">
        <button
            class="bg-sky-500 hover:bg-sky-700 text-white text-xs font-bold py-2 px-4 rounded-lg transition duration-300"
            @click="previousPage" :disabled="currentPage === 1">
            Anterior
        </button>

        <span class="mx-2">
            Pgina {{ currentPage }} de {{ totalPages }}
        </span>

        <button
            class="bg-sky-500 hover:bg-sky-700 text-white text-xs font-bold py-2 px-4 rounded-lg transition duration-300"
            @click="nextPage" :disabled="currentPage === totalPages">
            Siguiente
        </button>
    </div>
</template>
  
<script>
import Swal from 'sweetalert2';
export default {
    props: {
        data: Array,
        headerList: Array,
    },
    data() {
        return {
            itemsPerPage: 10,
            currentPage: 1,
        };
    },
    computed: {
        totalPages() {
            return Math.ceil(this.data.length / this.itemsPerPage);
        },
        paginatedItems() {
            const startIndex = (this.currentPage - 1) * this.itemsPerPage;
            const endIndex = startIndex + this.itemsPerPage;
            return this.data.slice(startIndex, endIndex);
        },
    },
    methods: {
        previousPage() {
            if (this.currentPage > 1) {
                this.currentPage--;
            }
        },
        nextPage() {
            if (this.currentPage < this.totalPages) {
                this.currentPage++;
            }
        },
        viewImage(pathImage) {
            const imageUrl = `http://localhost:3020/efuel/proveedor/report/imagen/${pathImage}`;

            Swal.fire({
                imageUrl: imageUrl,
                imageAlt: 'Imagen',
                showCloseButton: true, // Muestra un botn para cerrar el modal
            });
        }
    },
};
</script>

<style lang="scss">
.table-container {
    overflow-x: auto;
}

@media screen and (max-width: 660px) {
    .container-nav-pages {
        width: 500px;
    }
}
</style>